"use client";

import React from "react";
import ProgressBar from "./ProgressBar";
import FeedbackTooltip from "./feedbackTooltip";
import { JobCardProps } from "@/app/utility/props";
import { MapPin, Briefcase, AlertCircle } from "lucide-react";

export default function JobCard({ job }: JobCardProps) {
  const jobData = job.jobId; // easier alias

  return (
    <div className="bg-white border rounded-xl shadow-sm hover:shadow-md transition-all p-6 relative flex flex-col justify-between">
      {/* Top Section - Job Details */}
      <div>
        <div className="flex justify-between items-start mb-3">
          <div>
            <h2 className="text-lg font-semibold text-gray-800">
              {jobData?.title || "Untitled Job"}
            </h2>
            <p className="text-sm text-gray-500">{jobData?.companyName}</p>
          </div>
        </div>

        <div className="flex flex-wrap items-center gap-4 text-gray-600 text-sm mt-2">
          <span className="flex items-center gap-1">
            <MapPin size={16} className="text-blue-600" />
            {jobData?.location || "N/A"}
          </span>
          {jobData?.experience && (
            <span className="flex items-center gap-1">
              <Briefcase size={16} className="text-green-600" />
              {jobData.experience}
            </span>
          )}
        </div>

        <p className="mt-3 text-gray-700 text-sm leading-relaxed line-clamp-3">
          {jobData?.description || "No description provided."}
        </p>
      </div>

      {/* Bottom Section - Progress + Feedback */}
      <div className="mt-5">
        <div className="flex justify-between items-center mb-2">
          <span className="text-sm font-medium text-gray-800">
            Application Status:
          </span>
          <span
            className={`text-sm font-semibold capitalize ${
              job.status === "accepted"
                ? "text-green-600"
                : job.status === "pending"
                ? "text-yellow-600"
                : "text-red-600"
            }`}
          >
            {job.status}
          </span>
        </div>

        <ProgressBar status={job.status} />

        {job.status === "rejected" && job.adminFeedback && (
          <div className="mt-3 flex items-center gap-2 text-sm text-red-500">
            <AlertCircle size={16} />
            <FeedbackTooltip feedback={job.adminFeedback} />
          </div>
        )}
      </div>
    </div>
  );
}
